    menu.append($('<li></li>').html('<span class="ansi-magenta-fg">&lt;<a href="" class="ansi-bright-yellow-fg" data-attribute="quit">Q</a>&gt;</span> <span class="ansi-bright-yellow-fg">Leave this Planet</span>'))
